import React  from 'react';
import { message, Button } from 'antd';
import gql from 'graphql-tag';
import { useMutation } from '@apollo/react-hooks';
import axios from  'axios';


const ADD_MEMBER = gql`
    mutation($name: String,$phone:String,$age:Int,$gender: String,$email: String,$password: String){
        add_member(name: $name,phone: $phone, age: $age, gender: $gender, email: $email, password: $password){
            name
            
        }
    }
`;

export const ButtonCreateAccount = ( {name,email,age,gender,password,phone} ) => {
    
    const to_server = () => {
      axios.get(`http://localhost:3004/created?name=${name}`)
      .then(res => {
        console.log(res);
        res.data === "one" ? message.success(`Account ${name} created`) :  message.error('Name is invalid')  
      })
      
    };
    const [add_member, { data }] = useMutation(ADD_MEMBER,{
        variables: { name,phone,age,gender,email,password },
        onCompleted: () => to_server()
    });
   
    
    return (
      <Button type="primary" 
        disabled = {name.length < 3}
        onClick = {() => {add_member({variables:{name:name,phone:phone,age:Number(age),
            gender:gender,email:email,password:password}})}}
        style = {{display:'inline-block',marginLeft: '10%',width:'40%'}} 
      >
        Create_Account
      </Button>
    )
};