 import React from 'react';
import axios from 'axios';
import {  Modal, Input } from 'antd';




export const ModalAdd =( { modal , set, member, mode } ) =>  {
const [name_catalog,set_name_catalog] = React.useState(''); 
const create_catalog =()=>{
  axios.get('http://localhost:3004/createdir',{
    params: {
      member: member,
      name: name_catalog
    }
  })
} ;
 

return ( <Modal visible = {modal} 
      title = {`${mode}  FOLDER`}
      onCancel={()=>set(false)} 
      onOk={(e)=>{create_catalog();set(false)}}> 
           Name of catalog: 
           <Input
             placeholder="input folder name"
             allowClear
             value= {name_catalog} 
             type= "text" 
             style={{width: 200}} 
             onChange={(event)=>{set_name_catalog(event.target.value)}}
            />

    </Modal>   
    
  
);
        
};

 
 
 