import React, {useEffect} from 'react';
import "antd/dist/antd.css";
import gql from 'graphql-tag';
import {  Icon,  Tooltip,  Popover, Row, Col } from 'antd';
import { Link } from 'react-router-dom';

import { ModalAdd } from './modalAddCatalog';
import { StatDir } from './stat_dir';
import axios from 'axios';
import { stat } from 'fs';

const LIST_DIR = gql`
    query ($name: String) {
      resive_list_dir(name: $name) {
        list
      }
    }
  `;

const MainMenu = ( { name , set } ) => { 
    const [modal_visible, set_modal_visible] = React.useState(false);    
    const [modal_mode, set_modal_mode] = React.useState('add');
    const [reboot, set_reboot] = React.useState('');
    const [folderStat, set_folder_stat] = React.useState(''); 
   

    const height = window.innerHeight;
    //Query to server for list folders
    const [folders, set_folders] = React.useState(['']); 

    useEffect(()=>{
	    axios.get(`http://localhost:3004/member?name=${name}`)
		  .then( res => {
			set_folders(res.data);
		  });
    },[reboot]);

    useEffect(()=>{
	    axios.get(`http://localhost:3004/member?name=${name}`)
		  .then( res => {
			set_folders(res.data);
		  });
    },[]);
    useEffect(()=>{
	    axios.get(`http://localhost:3004/member?name=${name}`)
		  .then( res => {
			set_folders(res.data);
		  });
    },[modal_visible]);


    //Query to server for stat folders
    const stat = (a) =>{
    axios.get('http://localhost:3004/stat',{
      params: {
        member:name,
        folder:a
      }
    })
    .then( res => {
    set_folder_stat(res.data);
    });
  }


    return <div style={{background:'#fff',height: height,padding:'100px'}}>
      
      
        
       
        
        <Row type="flex" justify="center" align="middle"> 
            <Col lg = {{span : 6, offset : 1}}>
              <Tooltip title="close account">
                <Icon type="folder-open" style={{fontSize:100,color: "#ccc"}} 
                  onClick={()=>set('guest')}  
                />
              </Tooltip>   
            </Col>        
            {folders.map( i =>            
            <Col lg = {{span : 6, offset : 1}} key={i} onMouseEnter={()=>stat(i)}> 
             <Popover title = {`Folder ${i}: ${folderStat} files`} 
              content = {<StatDir  name = {i} member = {name} list = {folders} rebootin = {set_reboot}/>}
              placement="left"
            >              
              <Link to = { `/inside/${name}/${i}` } style={{ fontSize:30,fontWeight: 400 }}>  
               
                  <Icon type="folder" style={{fontSize:100,color: "#ccc"}}/>                   
                  {i}
               
              </Link>
              </Popover> 
            </Col>       
            )} 
            <Col lg = {{span : 6, offset : 1}}>
              <Tooltip title="add folder">
                <Icon type="folder-add" style={{fontSize:100,color: "#ccc"}} 
                  onClick={()=>{set_modal_visible(true);set_modal_mode('add')}}   
                />
              </Tooltip>   
            </Col>        
        </Row>       
      <ModalAdd  modal = {modal_visible} set={set_modal_visible} member = {name} mode = { modal_mode }/>
	</div>			
};
export {MainMenu, LIST_DIR};