import React ,{useState} from 'react';
import { Input ,Button, Form, Radio} from 'antd';
import { Link } from 'react-router-dom';
import gql from 'graphql-tag';
import { graphql } from 'react-apollo';


const EditAccount = (props) => {
    const { mutate } = props;
   
    const [name,setName] = useState('');
    const [phone,setPhone] = useState('');
    const [age,setAge] = useState();
    const [gender,setGender] = useState('male');
    const [email,setEmail] = useState('');
    const [password,setPassword] = useState('');
    return (     
        
        <Form 
        style={{textAlign:'center',font:"600 18px/20px 'Arial',sans-serif",
        margin:'100px auto',width:'70%',border:'1px solid #ddd',padding: 20,backgroundColor:"#ccc"
        }}
             labelCol={{ span: 7 }} wrapperCol={{ span: 12 }}>
            <p>Edit account {name} </p>
            <Form.Item label="Name">
            <Input value={name} onChange={(event)=>setName(event.target.value)} />
           
            </Form.Item >
            <Form.Item label="Phone">           
                <Input value={phone} onChange={(event)=>setPhone(event.target.value)} />
           
            </Form.Item>
            <Form.Item label="Age">
           
                <Input value={age} onChange={(event)=>setAge(event.target.value)} />
          
            </Form.Item>
            <Form.Item label="Gender">
            
               <Radio onChange={()=>setGender('men')} checked={gender==='men'?true:false}>Male</Radio>
               <Radio onChange={()=>setGender('women')} checked={gender==='women'?true:false}>Female</Radio>
           
            </Form.Item>
            
            <Form.Item label="Email">
            
                <Input value={email} onChange={(event)=>setEmail(event.target.value)}/>
           
            </Form.Item>

            <Form.Item label="Password">
            
                <Input value={password} onChange={(event)=>setPassword(event.target.value)}/>
           
            </Form.Item>
           
            <Button size="large" style={{display:'inline-block'}} ><Link to="/">cancel</Link></Button>
            <Button type="primary" 
                disabled = {name.length < 2}
                onClick={() => {mutate({variables:{name:name,phone:phone,age:Number(age),gender:gender,email:email,password:password}})}}
                style={{display:'inline-block',marginLeft: 10}}
                size="large"
            >
              Edit_Account
            </Button>
              
        </Form>
        
   
    );
    
   
   
     
};

const Edit_MEMBER = gql`
    mutation($name: String,$phone:String,$age:Int,$gender: String,$email: String,$password: String){
        edit_member(name: $name,phone: $phone, age: $age, gender: $gender, email: $email, password: $password){
            name
            phone
            age
            gender
            email
            password
        }
    }
`;

export default graphql(Edit_MEMBER)(EditAccount);    
