import React ,{useState} from 'react';
import {  Icon, Input ,Button, Form, Radio, Slider, Tooltip } from 'antd';
import { Link } from 'react-router-dom';

import { ButtonCreateAccount } from './component/buttom_create_account';


const CreateAccount = () => {   
    const [name,setName] = useState('');
    const [phone,setPhone] = useState('');
    const [age,setAge] = useState(20);
    const [gender,setGender] = useState('men');
    const [email,setEmail] = useState(`${name}@tut.by`);
    const [password,setPassword] = useState(name);
    const onChange = (value) => {
        setAge(value)
    }
    

    return (     
        
        <Form 
          style={{textAlign:'center', margin:'0 auto', width:'40%',position: 'absolute',
            top:'10%',left:"30%",
            border:'5px solid #ccc', padding: 20, backgroundColor:"#ddd"
          }}
          labelCol={{ span: 7 }} wrapperCol={{ span: 12 }}
        >
            <h3 style={{textAlign:"left",fontWeight: 600}}>Create_Account</h3>
            <hr/>
            <Form.Item label="Name">
            <Input value={name} onChange={(event)=>setName(event.target.value)} 
                 prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }}/>}
                 placeholder = "Username"
            />
           
            </Form.Item >
            <Form.Item label="Phone">           
                <Input value={phone} onChange={(event)=>setPhone(event.target.value)}
                 prefix={<Icon type="phone" style={{ color: 'rgba(0,0,0,.25)' }} />}
                 placeholder="Phone"
            />
           
            </Form.Item>
            <Form.Item label="Age">
           
                <Slider  onChange={onChange} defaultValue={20} tooltipVisible value={age}/>
          
            </Form.Item>
            <Form.Item label="Gender">
              <Tooltip title = "man">
               <Radio onChange={()=>setGender('men')} checked={gender==='men'?true:false}>
                    <Icon type="man" style={{ color: 'rgba(0,0,0,.25)', fontSize: 25 }} />
               </Radio>
              </Tooltip>
              <Tooltip title = "women" overlayStyle={{color:"red"}}>
               <Radio onChange={()=>setGender('women')} checked={gender==='women'?true:false}>
                    <Icon type="woman" style={{ color: 'rgba(0,0,0,.25)' ,fontSize: 25}} />
                </Radio>
                </Tooltip>     
            </Form.Item>
            
            <Form.Item label="Email">
            
                <Input value={`${name}@tut.by`} onChange={(event)=>setEmail(event.target.value)}
                
                 placeholder="@ Email"
                
                />
           
            </Form.Item>

            <Form.Item label="Password">
            
                <Input value={password} onChange={(event)=>setPassword(event.target.value)}
                 prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                 placeholder="Password"
                />
           
            </Form.Item>
            <hr/>
            <p style={{width:'70%',margin:'10px auto'}}>
                <Button  style={{display:'inline-block',width:'40%'}} ><Link to="/">cancel</Link></Button>
                <ButtonCreateAccount name={name} phone={phone} password={password} age={age} email={email} gender={gender} />
            </p> 
        </Form>
        
   
    );
    
   
   
     
};



export default (CreateAccount);    
