import React from 'react';
import "antd/dist/antd.css";

import { Button } from 'antd';

import axios from 'axios';


export const ButtonDeleteFile = ({name, member, file}) => {
  
  const deleteFile =() =>{
    axios.get('http://localhost:3004/delete',{
      params:{
        member: member,
        name:name,
        file: file
      }
    })
  };

   
     return (          
        <Button type="danger"
          size="small"
          icon="delete"
          onClick = {()=> deleteFile()} 
        />   
     
      )
};       