import React from 'react';
import "antd/dist/antd.css";
import axios from 'axios';
import { Button, Popconfirm} from 'antd';
import { ButtonRenameFolder } from './component/button_rename_folder';



export const StatDir = ( { name, member, list, rebootin } ) => { 
    
   
    
    const delete_dir = () => {
      axios.get('http://localhost:3004/deletedir',{
      params: {
        member:member,
        name:name
      }
    })

    };


    
   
    return <>
       
        <ButtonRenameFolder   name= {name}  member={member} list = {list}  reb = { rebootin }/>
        <Popconfirm placement="rightTop" title="Delete folder and all files" onClick={()=>delete_dir()}>
          <Button type="primary" style={{margin:"10px 0",width:"100%",display:"block"}}  >
            Delete
          </Button>
        </Popconfirm>
       
    </>
};