import React from 'react';
import gql from 'graphql-tag';
import { useQuery } from '@apollo/react-hooks';
import { message  } from 'antd';
const ACCOUNT_QUERY = gql`
    query ($name: String, $password: String) {
      member_verify(name: $name, password: $password) {
        name
        password
      }
    }
  `;

 
export const Bob =( { set_member, name, password , set_bob } ) =>  {
  const error = () => {
    message.error(<h3>The name or password are invalid</h3>).then( () =>
    {
      set_bob(false)
    });  
  };
  
  
const {loading, data } =  useQuery(ACCOUNT_QUERY,{
  variables: { name, password },
  fetchPolicy: 'network-only'
});
     
  if ( loading ) return <p>One moument</p>;
  if ( data.member_verify[0].name === "guest" ) { error() };
  
  
return ( <>
    <span> {set_member(data.member_verify[0].name)}</span>
    
  </>
);
        
};
