import React from 'react';
import "antd/dist/antd.css";
import { Input, Icon, Button, Tooltip } from 'antd';
import { Link } from 'react-router-dom';
import { Bob } from './bob';





const SignInWindow = ( {set} ) =>{
 
  const [input_name,set_input_name] = React.useState('');
  const [input_password,set_input_password] = React.useState('');
  const [bob_visible, set_bob_visible] = React.useState(false);
  const divStyle = {  margin:'0 auto', width:'30%', 
                      padding: 40,backgroundColor:"#ddd",
                      position: 'absolute', top: '10%',left:'35%'
                    } ;
    
         

  return  <div style={divStyle}>
          <h5>Sign in to your account <br/>
            <Tooltip title="create account">
             <span style={{fontSize:15}}> No account ? </span><Link to='/account_create' style={{fontSize:15}}> Create account</Link>
            </Tooltip>
          </h5>
          <hr/>
          <h6 style={{margin:'10px auto'}}>Name *</h6>
          <Input 
            required
            onChange={(event)=>set_input_name(event.target.value)}
            value={input_name}
            size="large"
            prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
            placeholder="Username"
            style={{margin:'10px auto',display: 'block'}} required     
          />

          <h6 style={{margin:'10px auto'}}>Password *</h6>
          
          <Input 
            required
            onChange={(event)=>set_input_password(event.target.value)}
            value={input_password}
            size="large"
            prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
            type="password"
            placeholder="Password"
            style={{margin:'10px auto',display: 'block'}} required
          />
          <p style={{margin:'10px auto'}}>
            Forget password ? <Link to="/">Reset password</Link>
          </p>           
          <hr/>
          
               
          <Button type="primary" style={{width:'50%'}}
            disabled={(input_name.length < 3) || (input_password.length < 3)}
            onClick={() => { set_bob_visible(true) }}
          >                
            Sign_in 
          </Button>        
          { bob_visible && <>
              <Bob  name={input_name} password={input_password} set_member={set} set_bob ={set_bob_visible} />  
            </>
          }           
        </div>
      
        };
  export {SignInWindow};