import React from 'react';
import "antd/dist/antd.css";
import gql from 'graphql-tag';
import { Button,  Icon } from 'antd';
import { useMutation } from '@apollo/react-hooks';



const List_FILES = gql`
    mutation ($file: Upload!,$name: String, $member: String, $file_name: String) {
      uPload(file: $file,name: $name, member: $member, file_name: $file_name) {
        filename
       
      }
    }
`;
export const ButtonUploadFile = ({name, member}) => {
  const [file, set_file ] = React.useState('');
  const [file_name,set_file_name] = React.useState('');
  
   function preview(e) {
    var preview = document.getElementById("img");
   
    var file1 = document.getElementById("upload_file").files[0];
    var reader = new FileReader();   

    reader.onload = function (e) {    
      console.log(`Name:${file1.name},Type file:${file1.type},${file1.size}`);
      set_file(reader.result);
      set_file_name(`${file1.name}`);
      preview.style.backgroundImage = `url(${reader.result})`;
     
      
    }
    reader.readAsDataURL(file1);    
    
  };

  const  [ uPload, {data}, loading ,error] = useMutation( List_FILES,{
    variables: {file: file, name: name, member: member, file_name: file_name},
    refetchQueries: () => [{ variables: {name:name, member:member} }],
    onCompleted:() => (set_file(''),document.getElementById('img').style.backgroundImage = null,
    set_file_name(''))
  }); 
    if ( loading ) return <p>Loading</p>;
    if ( error ) return <p>error.message</p>;    
     return (      
        
      <div   id="img" 
        style={{height: 200,
        backgroundSize: "100%",
        backgroundPosition: "center",
        backgroundColor: "rgba(255,255,0, 0.5)",
        backgroundRepeat: "no-repeat",
        width: 200, margin: 10,border:"2px solid #ccc"}}
      > 
      { !file_name && 


        <label htmlFor = "upload_file" > 
        <input type="file" 
          id="upload_file"
          style={{display:"none"}}
          accept=".jpg,.jpeg"        
          onChange = {(e)=>preview(e)}          
        />          
          <Icon type ="upload" style={{fontSize:40,margin:"80px auto 10px",cursor:"pointer"}} />
        </label> 
        
         }
       { file_name &&  
        <Button type="primary"
          style={{margin:"80px auto 10px"}}
          onClick = {()=> uPload()}>
          upload
        </Button> 
       }
     </div>
      )
};       