import React from 'react';
import "antd/dist/antd.css";
import axios from 'axios';
import { Button, Input } from 'antd';






export const ButtonRenameFolder = ({ name, member , list, reb }) => {
    const [ new_name, set_new_name ] = React.useState(''); 
    const onChange =(e) => {
        set_new_name(e.target.value)
    }  
    //Query to server for rename folders
    const renameFolder = () => {

axios.get('http://localhost:3004/rename_folder', {
  params: {
    member:member,
    name:name,
    new_name: new_name
  }
}).then( res => {
  reb(res.data);
});


    }
       return (
           <> 
            <Button type="primary" style={{width:"100%",margin:"10px 0"}}
              disabled = {(list.indexOf(new_name) > 0 || new_name.length < 1)}
              onClick = {() => renameFolder()}>
                rename
            </Button>          
           <Input type="text" 
                placeholder="New name"
                value={new_name}
                onChange = {onChange}
            />            
        </>
       )
};       