import React ,{useEffect} from 'react';
import "antd/dist/antd.css";
import axios from 'axios';
import { Carousel ,Row, Col ,  Icon, Popover, Card} from 'antd';

import { Link } from 'react-router-dom';
import { ButtonUploadFile } from './Bottom_file_upload';
import { ButtonDeleteFile } from './component/button_delete_file';



export const InsideFolder = ( {name, member} ) => {
    const height = window.innerHeight - 10;
    const [visible, set_visible] = React.useState(true);
    const [size, set_size ] = React.useState(100);
    const [list, set_list] = React.useState(['']);

   useEffect(()=>{
	    axios.get('http://localhost:3004/list',{
      params:  {
      name: name,
      member: member
      }
    })  
		  .then( res => {
			set_list(res.data);
		  });
    },[]); 
       
    
    return <div>
    {visible ? 
     <Row   justify="center" type="flex" >
      <Col lg = {{ span : 4 }} 
        style={{textAlign:"center",margin:10  ,border:"2px solid #ccc",height: 200, width: 200,}}
      >         
        <Link to = "/main" name = { member }>
         <Icon type="arrow-left" style={{font:"400 40px/200px 'Arial', sans-serif", cursor:"pointer",  }} />
        </Link>       
      </Col>
      
        {list.map( (i,index) =>
          
          <Col lg = {{ span : 4 }} key={index} > 
            <Popover  placement="topRight" content={<ButtonDeleteFile name={name} member={member} file={i}/>} >
              <Card               
                hoverable = "true"             
                onClick = {()=> set_visible(false)}
                style={{background:`url(http://localhost:3004/members/${member}/${name}/${i}) no-repeat  center/auto 100%`,
                height: 200,margin: 10 , cursor: 'pointer'}}
              >   
              </Card>
            </Popover>   
          </Col>
           
        )}
        <Col lg = {{ span : 4 }} style={{textAlign:"center"}}>
         
         
          <ButtonUploadFile name={name} member={member} />
        
        </Col>
    </Row> :

    <Carousel autoplay dotPosition="bottom">        					
		 { list.map((i,index) => <div style={{margin: 0}}  key={index}>
			  <div style={{background:`url(http://localhost:3004/members/${member}/${name}/${i}) no-repeat  center/auto ${size}%`,
           height: height ,margin: 0}}
           onClick = {() => set_visible(true)}
           onWheel = {() => set_size( size < 160 ? size + 10 : 100  )}
           title = "click:back,leave:slide"
        >   
        </div>
		  </div>
     )}
    </Carousel>
    }
   </div>
   
}